// SB Overlay:
// --sb-overlay-z-index - Sets z index of the overlay
// --sb-overlay-dark-color - dark overlay color in RGB triplet (ex: 0, 0, 0)
// --sb-overlay-light-color - light overlay color in RGB triplet (ex: 255, 255, 255)

.sb-overlay {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  z-index: var(--sb-overlay-z-index, var(--sbt-stack-overlay));
}

.sb-overlay_dark {
  background-color: rgba(var(--sb-overlay-dark-color, 0, 0, 0), 0.5);
}

.sb-overlay_light {
  background-color: rgba(var(--sb-overlay-light-color, 255, 255, 255), 0.5);
}
